nav {
  position: fixed;
  display: grid;
  grid-row: 1 / 2;
  grid-column: 1 / 3;
  grid-template-columns: 15% 15% 15% 55%;
  justify-self: center;
  width: 90%;
  max-width: 1920px;
  top: 1rem;
  z-index: 1;
  font-weight: bold;
  color: rgb(255, 255, 255);
}

nav a {
  justify-self: start;
  align-self: center;
  padding: 0.5rem 1rem;
  color: rgb(255, 255, 255);
  text-decoration: none;
  border-radius: 25px;
  text-shadow: 2px 1px 1px rgba(0, 0, 0);
  box-shadow: 0px 0px 0px 0px rgb(255, 255, 255);
  transition: all 0.25s ease-in;
}

/* Download CV Link */
nav a:nth-child(4n) {
  display: flex;
  justify-self: end;
}

nav a:hover {
  cursor: pointer;
  box-shadow: 0px 0px 0px 2px rgb(255, 255, 255);
  background-color: rgba(0, 0, 0, 0.5);
}

nav a svg {
  height: 1.25rem;
  width: auto;
  padding: 0 0.25rem 0 0;
}
